@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Futura PT';
  src: url("../fonts/Futura-PT-Heavy.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1320px;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

li {
  list-style: none;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.row.space-between {
  justify-content: space-between;
}

.row.align-center {
  align-items: center;
}

.row.nowrap {
  flex-wrap: nowrap;
}

h1,
h2,
h3,
h4 {
  font-family: 'Futura PT';
  font-weight: 700;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 4rem;
}

.btn-wrap {
  position: relative;
}

.btn {
  position: relative;
  display: inline-block;
  padding: 19px 24px 20px 30px;
  font-weight: 500;
  font-size: 1.2rem;
  text-decoration: none;
  color: #000;
  background: linear-gradient(to left, #FFF9C6, #FFD8AB);
}

.btn::after {
  content: "";
  position: absolute;
  right: -28px;
  top: 0;
  border-style: solid;
  border-width: 27px 0 27px 28px;
  border-color: transparent transparent transparent #fff9c6;
  width: 0;
  height: 0;
}

.btn__rect {
  position: absolute;
  left: 3px;
  top: -35px;
  width: 214px;
  height: 124px;
  fill: transparent;
  stroke: #fcf3a2;
}

.header {
  padding: 10px 0;
  box-shadow: 0 1px 1px 0 #000;
  background-color: rgba(13, 18, 27, 0.9);
}

.header__logo {
  width: 120px;
}

.header__logo img {
  max-width: 100%;
}

.header__nav-col {
  display: flex;
  flex-direction: column;
}

.header__nav {
  display: flex;
}

.nav__item {
  position: relative;
  margin-bottom: 3rem;
}

.nav__item:first-child {
  margin-bottom: 1rem;
}

.nav__link--triangle::after {
  content: "";
  position: relative;
  right: -10px;
  top: 13px;
  border-style: solid;
  border-width: 5px 5px 0;
  border-color: #fcf3a2 transparent transparent;
  width: 0;
  height: 0;
}

.nav__link {
  font-weight: 400;
  font-size: 1.5rem;
  text-decoration: none;
  color: #fff;
  transition: 0.3s;
}

.nav__link:hover {
  color: #fcf3a2;
  opacity: 1;
}

.submenu {
  position: relative;
  left: 10px;
  display: block;
  padding: 2rem;
  border-radius: 10px;
  background-color: #0d121b;
  transition: 0.3s;
}

.submenu__item:not(:last-child) {
  margin-bottom: 2.5rem;
}

.nav__item:first-child:hover .submenu {
  display: block;
}

.description {
  order: -1;
  margin-bottom: 3rem;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 1.6;
  color: #fff;
}

.description__icon {
  margin-right: 1rem;
  margin-bottom: 1rem;
  width: 15px;
  height: 15px;
  fill: #fcf3a2;
}

.description__text a {
  margin-left: 0.5rem;
  color: #fcf3a2;
}

.description__text a:hover {
  color: #FFD8AB;
}

.phones {
  position: relative;
}

.phones__icon {
  z-index: 2;
  margin-top: 10px;
  margin-right: 2.4rem;
  width: 21px;
  height: 38px;
  fill: #fcf3a2;
}

.phones__icon-rect {
  position: absolute;
  left: -6px;
  top: 20px;
  z-index: 1;
  width: 39px;
  height: 38px;
  fill: transparent;
  stroke: #fcf3a2;
}

.phones__link {
  font-size: 1.4rem;
  line-height: 3.2rem;
  text-decoration: none;
  color: #fff;
}

.phones__link ~ span {
  margin-left: 1rem;
  font-size: 1.2rem;
  color: #BDBDBD;
}

.burger-btn {
  position: relative;
  display: block;
  width: 30px;
  height: 19px;
  cursor: pointer;
}

.burger-btn span {
  position: absolute;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #fcf3a2;
  transform: rotate(0deg);
  transition: 0.3s;
}

.burger-btn span:nth-child(1) {
  top: 0;
}

.burger-btn span:nth-child(2) {
  top: 9px;
}

.burger-btn span:nth-child(3) {
  top: 9px;
}

.burger-btn span:nth-child(4) {
  top: 18px;
}

.burger-btn--active span:nth-child(1) {
  width: 0;
}

.burger-btn--active span:nth-child(2) {
  transform: rotate(45deg);
}

.burger-btn--active span:nth-child(3) {
  transform: rotate(-45deg);
}

.burger-btn--active span:nth-child(4) {
  width: 0;
}

@media (min-width: 576) {
  .btn {
    padding: 21px 37px 24px 43px;
    font-size: 1.4rem;
  }
}

@media (min-width: 576px) {
  .header__logo {
    width: 150px;
  }
}

@media (min-width: 992px) {
  .btn::after {
    right: -28px;
    border-width: 29px 0 29px 28px;
  }

  .btn__rect {
    width: 260px;
    height: 129px;
  }

  .btn {
    font-size: 1.6rem;
  }
}

@media (min-width: 1200px) {
  .header__nav-col {
    margin-right: 5.2rem;
  }

  .header__btn {
    display: none;
  }

  .header {
    padding: 2.7rem 0;
  }

  .nav__list {
    display: flex;
  }

  .nav__item {
    margin-bottom: 1.8rem;
  }

  .nav__item:not(:first-child) {
    margin-left: 3.5rem;
  }

  .nav__link {
    opacity: 0.5;
  }

  .nav__btn {
    display: none;
  }

  .submenu {
    position: absolute;
    display: none;
    background-color: #252931;
  }

  .description__icon {
    margin-bottom: 0;
  }

  .description {
    order: 1;
    margin-bottom: 0;
  }

  .phones__icons-wrap {
    display: none;
  }

  .burger-btn {
    display: none;
  }
}

@media (min-width: 1280px) {
  .header__logo {
    width: 170px;
  }

  .phones__link {
    font-size: 1.8rem;
  }

  .phones__link ~ span {
    font-size: 1.4rem;
  }
}

@media (min-width: 1366px) {
  .phones__icons-wrap {
    display: block;
  }
}

@media (max-width: 1199px) {
  .header__nav {
    position: absolute;
    right: 0;
    top: 72px;
    display: none;
    padding: 3.1rem 3.9rem 3.1rem 5.1rem;
    width: 39rem;
    background-color: #0d121b;
    transition: all 0.3s;
  }

  .header__nav--active {
    display: block;
  }

  .phones {
    margin-bottom: 3rem;
  }
}

@media (max-width: 576px) {
  .header__nav {
    top: 63px;
    width: 100%;
  }
}